import React, { Component } from 'react';
import styled from 'styled-components';
import toastr from 'toastr';

import { requestor } from '../common';

import ApplicationFormLayout from '../Component/ApplicationFormLayout';

import InputBox from '../Component/InputBox';
import AlertMessageBox from '../Component/AlertMessageBox';

const SubmitButton = styled.button`
    border: none;
    width: 100%;
    text-align: center;
    padding: 16px 0;
    font-size: 14px;
    color: #1f1f1f;
    margin-top: 34px;
`;

const AlertConfirmButton = styled.button`
    border: none;
    background: #999999;
    width: 100%;
    margin-top: 20px;
    font-size: 14px;
    color: #ffffff;
    padding: 12px 0;
`;

export default class extends Component{
    constructor() {
        super();

        this.onChangeInput = this.onChangeInput.bind(this);
        this.state = {
            isAlertOpen: false,
            formData: {
                email: '',
                password: ''
            }
        }
    }

    render() {
        const passwordIconStyle = {
            width: '14px',
            height: '20px',
            margin: '14px 0'
        };
        const inputStyle = {
            width: '100%'
        };

        return(
            <section>
                {this.state.isAlertOpen && (<AlertMessageBox title={this.state.alertTitle} message={this.state.alertMessage}>
                    <AlertConfirmButton onClick={this.onClickAlertConfirmButton.bind(this)}></AlertConfirmButton>
                </AlertMessageBox>)}
                <ApplicationFormLayout onSubmit = {this.onSubmitApplyForm.bind(this)} formDescription="    ." formTitle="">
                    <div>
                        <InputBox inputType="email" iconImage="ic-person.png" iconStyle={passwordIconStyle} placeholder=" ." style={inputStyle} name="email" onChange={this.onChangeInput} value={this.state.formData.email}/>
                        <InputBox inputType="password" iconImage="ic-lock.png" iconStyle={passwordIconStyle} placeholder=" ." style={inputStyle} name="password" onChange={this.onChangeInput} value={this.state.formData.password}/>

                        <SubmitButton type="submit"></SubmitButton>
                    </div>
                </ApplicationFormLayout>
            </section>
        )
    }

    async onChangeInput(event) {
        const { name:inputName, value } = event.target;
        
        await this.setState({
            formData: {
                ...this.state.formData, 
                ...{ [inputName]: value }
            }
        });
    }

    onSubmitApplyForm(event) {
        event.preventDefault();

        requestor({ 
            url: '/api/apply/status',
            method: 'POST',
            headers: {
                'Access-Control-Allow-Origin': '*',
                'Content-Type': 'application/json'
            },
            crossDomain: true,
            data: this.state.formData
        })
        .then(response => {
            const { isAccepted, name, waitingNumber } =  response.data;

            if(isAccepted) {
                const alertMessage = !!waitingNumber ? `${name}   ${waitingNumber}.` : `${name}  .`

                this.setState({ isAlertOpen: true, alertTitle: '', alertMessage });
                
                return;
            }
            
            this.setState({ isAlertOpen: true, alertTitle: '', alertMessage: '  .' });
        })
        .catch(error => {
            const errorData = error.response.data;
            const { message } = errorData;

            toastr.error(message);
        });
    }

    onClickAlertConfirmButton() {
        this.setState({ isAlertOpen: false });
    }
}


