import React, { Component } from 'react';
import { IMAGE_HOST } from '../config';

export default class extends Component {
    render() {
        const { children: form, pageName, imageName, formStyle = {}} = this.props;

        return(
            <section className={`container-form-${pageName}`}>
                <div className="wrap-form">
                    {form}
                </div>
                <div className="wrap-background-image" style =  {{ backgroundImage: `url(${IMAGE_HOST}/${imageName})` }}></div> 
            </section>
        )
    }
}


