import React from 'react';
import styled from 'styled-components';

import { Link } from 'react-router-dom';

const MainView = styled.section`
    background-color: #292929;
    overflow: hidden;
    height: 100vh;
    font-family: NotoSansCJKkr;
  .bg{
      width:50%;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      float: left;
      box-sizing: border-box;
      padding-left: 50px;
  }
  .highthon{
      font-size: 21px;
      line-height: 1.48;
      color: #ffffff;
  }
  .title{
    font-size: 46px;
    font-weight: bold;
    line-height: 1.26;
    color: #ffffff;
  }
  .explain{
    margin-bottom: 51px;
    font-size: 13px;
    font-weight: normal;
    line-height: 1.62;
    color: #ffffff;
  }
  .lnk-facebook{
    font-size: 17px;
    font-weight: 500;
    line-height: 1.23;
    color: #ffffff;
  }
  .btn{
        display: inline-block;
        font-weight: bold;
        font-size: 15px;
        color: #292929;
        width: 152px;
        padding: 16px 0;
        border: none;
        text-align: center;
        background-color: #ffffff;
        margin-right: 13px;
        margin-bottom: 25px;
  }
  .background{
    float: right;
    
    width:50%;
    height:100%;
    overflow: hidden;
    
    border-width: 4px;
    border-color: white;

    background: url('/images/bg-picture-main.png');
    background-size: cover;
  }
  .backgroundImg{
      width: 100%;   
  }

  @media(max-width: 450px) {
    .bg{
        width: 100%;
        padding-left: 20px;

        .highthon{
            margin-bottom: 20px;
        }
        .title{
            font-size: 34px;
        }

        .btn{
            width: 120px;
        }
    }
    .background{
        width: 0;
    }
  }
`

export default () => {
        return (
            <MainView>
                <div className="bg">
                    <p className="highthon"> : HighThon</p>
                    <h1 className="title"> <br/>  </h1>
                    <p className="explain">
                              .
                    </p>

                    
                    <Link to="/apply" className="btn"></Link>
                    <Link to="/apply/status" className="btn"></Link>
                    <br/>
                    <a href="https://www.facebook.com/highthon/" className="lnk-facebook">   <img src="/images/ic_arrow_down.png"/></a>
                </div>
                <div className="background">
                </div>
            </MainView>
    );
};


