import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import FormLayout from '../Component/FormLayout';
import InputBox from '../Component/InputBox';

export default class AdminSignUp extends Component{
    render() {
        const idIconStyle = {
            width: '17px',
            height: '17px',
            margin: '13px 0'
        };
        const passwordIconStyle = {
            width: '14px',
            height: '20px',
            margin: '14px 0'
        };

        return (
            <FormLayout pageName = "admin-signin"
                        imageName = "bg-picture-admin-signup.png">
                <div className="wrap-signin-form" style={{top: '40%'}}>
                    <h2> </h2>
                    <form>
                        <InputBox inputType="text" iconImage="ic-email.png" iconStyle={idIconStyle} placeholder=" ."/>
                        <InputBox inputType="password" iconImage="ic-lock.png" iconStyle={passwordIconStyle} placeholder=" ."/>
                        <InputBox inputType="text" iconImage="ic-person.png" iconStyle={passwordIconStyle} placeholder=" ."/>
                        <InputBox inputType="text" iconImage="ic-work.png" iconStyle={passwordIconStyle} placeholder=" ."/>
                        <InputBox inputType="tel" iconImage="ic-call.png" iconStyle={passwordIconStyle} placeholder=" ."/>
                        <InputBox inputType="email" iconImage="ic-email.png" iconStyle={idIconStyle} placeholder=" ."/>
                        <button type="submit" className="btn-submit"></button>
                    </form>
                    <p className="no-account">
                         ? <Link to="/admin" style={{color: '#ffffff', fontWeight: 'bold'}}></Link>
                    </p>
                </div>
            </FormLayout>      
        )
    }
}


