import React, { Component } from 'react';
import styled from 'styled-components';

const RadioBox = styled.div`
    overflow: hidden;
    width: 374px;
    box-sizing: border-box;
    padding: 14px 0 10px 16px;
    background-color:  #1f1f1f;
    margin: 8px auto;

    @media (max-width: 450px) {
        width: 100%;
    }
        
    .category{
        float: left;
        font-size: 14px;
        color: #ffffff;
    }

    .wrap-label{
        width: calc(100% - 30px);
        float: right;
        color: #ffffff;
        font-size: 13px;

        label{
            display: block;
            float: left;
            width: 50%;
            text-align: center
            color: #ffffff;

            input{
                margin-right: 6px;
            }
        }
    }
`;

export default ({ category = '', items = [] }) => {
    return(
        <RadioBox>
            <span className="category">{category}</span>
            <div className="wrap-label">
            {items.map(({label, name, checkboxStyle = {}, labelStyle = {}, onChange = () => {} ,value =''}, index) =>( 
                <label style={labelStyle} key={index}>
                    <input type="radio" name={name} style={checkboxStyle} onChange= {onChange} value = {value}/>
                    {label}
                </label>
            ))}
            </div>
        </RadioBox>
    )
}


