import React, { Component } from 'react';
import AdminSignIn from './AdminSignIn';
import axios from 'axios';

export default class AdminMain extends Component{
    constructor() {
        super();

        this.state ={
            isLogin: false
        };
    }
    componentDidMount() {
        
    }
    render() {
        return this.state.isLogin ? (<div></div>) :( <AdminSignIn/> );
    }
}


