import React, { Component } from 'react';
import styled from 'styled-components';

const ApplicationForm = styled.section`
    background: url('/images/bg-picture-form.png');
    background-size: cover;
    position: absolute;
    width: 100%;
    height: 100%;

    .wrap-form{
        height: 100%;
        width: 450px;
        padding: 0 38px
        background: #292929;
        box-sizing: border-box;
        margin: 0 auto;
        position: relative;

        .form{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            width: 374px;

            .form-title{
                font-size: 25px;
                color: #ffffff;
                text-align: center;
                position: relative;

                &::after{
                    display: block;
                    position: absolute;
                    bottom: -2px;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 30px;
                    height: 2px;
                    background: #ffffff;
                    content: '';

                }
            }

            .form-description{
                font-size: 16px;
                margin-top: 10px;
                margin-bottom: 42px;
                color: #ffffff;
                text-align: center;
            }
        }

        @media (max-width: 450px) {
            width: 100%;
            min-height: 100%;
            height: auto;

            .form{
                padding: 40px 0;
                position: static;
                transform: translate(0, 0);
                width: 100%;
            }
        }
    }
`;

export default ({ children, onSubmit, formTitle, formDescription }) => {
    return( 
        <ApplicationForm>
            <div className="wrap-form">
                <form className="form" onSubmit = {onSubmit}>
                    <h2 className="form-title">{formTitle}</h2>
                    <p className="form-description">{formDescription}</p>
                    {children}
                </form>
            </div>
        </ApplicationForm>
    );
};


