import React from 'react';
import { IMAGE_HOST } from '../config';
import styled from 'styled-components';

const InputBox = styled.div`
    overflow: hidden;
    width: 280px;
    box-sizing: border-box;
    padding-left: 16px;
    background-color:  #1f1f1f;
    margin: 8px auto;

    .icon-input{
        float: left;
    }
    .input{
        float: right;
        border: none;
        padding: 15px 0;
        width: calc(100% - 24px);
        background-color:  #1f1f1f;
        color: #ffffff;
        font-size: 13px;
        text-align: left;

        &::-moz-placeholder,
        &::-webkit-input-placeholder{
            color: #eeeeee;
        }
    }
`;

export default ({ iconImage = '', inputType, style = {}, iconStyle = {}, placeholder = '', name = '', onChange = ()=>{}, value = '' }) =>{
    const defaultIconStyle = {
            backgroundImage: `url(${IMAGE_HOST}/${iconImage})`,
            backgroundSize: 'cover',
            height: '20px',
            width: '20px',
            display: 'block',
            backgroundPosition: 'center center'
        };
    
        iconStyle = Object.assign(defaultIconStyle, iconStyle);

    return(
        <InputBox style={style}>
            <i className="icon-input" style={iconStyle}></i>
            <input type={inputType} className="input" placeholder={placeholder} name={name} onChange ={onChange}/>
        </InputBox>
    );
}


