import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'

import './scss/style.scss';
import './scss/toastr.scss';

import MainView from './Container/MainView';
import AdminMain from './Container/AdminMain'
import AdminSignUp from './Container/AdminSignUp';
import ApplicationForm from './Container/ApplicationForm';
import ApplicationStatusForm from './Container/ApplicationStatusForm';

class App extends Component {
  render() {
    return (
        <Router>
            <Switch>
                <Route exact path="/" component={MainView} />
                <Route exact path="/admin" component={AdminMain} />
                <Route path="/admin/signup" component={AdminSignUp} />
                <Route exact path="/apply" component={ApplicationForm}/>
                <Route path="/apply/status" component={ApplicationStatusForm}/>
            </Switch>
        </Router>
    );
  }
}

export default App;



