import React, { Component } from 'react';
import styled from 'styled-components';

const DimmedArea  = styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.8);
    display: block;
    z-index: 100;
`;

const AlertMessageBox = styled.div`
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 320px;
    background: #ffffff;
`;

const AlertMessageName = styled.div`
    background: #999999;
    color: #ffffff;
    font-size: 24px;
    padding: 16px 0 16px 12px;
`;

const AlertMessageContent = styled.div`
    padding: 16px 12px;
`;

export default ({ title = '', message = '', children}) => {
    return (
        <DimmedArea>
            <AlertMessageBox>
                <AlertMessageName>{title}</AlertMessageName>
                <AlertMessageContent>
                    <p>{message}</p>
                    {children}
                </AlertMessageContent>
            </AlertMessageBox>
        </DimmedArea>
    );
};


